import React, { useState } from "react";
import HeaderNav from "../components/common/HeaderNav";
import CustomImage from "../components/ui/CustomImage";

export default function Page() {
  const [inputFields, setInputFields] = useState({
    currency: "",
  });
  return (
    <div className="text-white">
      <div className="wrapper ">
        <HeaderNav />
        <div className="h-[100vh] w-full bg-[#1b1b1b] ">
          <div className="h-[250px] bg-[#1e1e1e] flex items-center">
            <div className="container px-[100px] flex justify-between items-center">
              <div className="max-w-[800px] ">
                <p className="font-nekstmedium text-[30px] mb-[20px]">
                  Используйте P2P для покупки и продажи USDT
                </p>
                <p className="font-medium text-[16px]">
                  Безопасный и легкий трейдинг без комиссии на Bitget P2P.
                  Торгуйте USDT с помощью различные способы оплаты, такие как
                  банковские переводы и электронные кошельки.
                </p>
              </div>
              <div className="p-[5px] bg-[#1b1b1b] rounded-[20px]">
                <CustomImage
                  width={350}
                  height={200}
                  alt="p2p"
                  src="/pages/p2p-trade/p2p-trade-icon.png"
                ></CustomImage>
              </div>
            </div>
          </div>
          <div className="container px-[100px]">
            <div className="flex justify-between items-center">
              <div className="flex w-[300px]">
                <button>Buy</button>
                <button>Sell</button>
              </div>
              <div className="flex items-center space-x-[10px]">
                <div className="flex">
                  <p>Merchant ads</p>
                  <div data-v-2b0615f0="" className="bit-switch">
                    <input
                      type="checkbox"
                      name=""
                      className="bit-switch__input"
                    />
                    <span className="bit-switch__core"></span>
                  </div>
                </div>
                <div className="relative w-[200px] border-solid border-[1px] border-[#888888] rounded-[20px] h-[35px] group">
                  <ul className="absolute invisible opacity-0 top-[35px] w-full bg-[#232323]  rounded-[10px] p-[5px] group-hover:visible group-hover:opacity-100">
                    <li className="w-full h-[50px] items-center flex  font-nekstmedium hover:bg-[#3c3c3c] rounded-[10px]">
                      <button className="w-full h-full flex items-center px-[15px]">
                        <div className="flex items-center">
                          <CustomImage
                            width={20}
                            height={20}
                            alt="USDT"
                            src="/pages/p2p-trade/usdt.png"
                            className="mr-[3px]"
                          ></CustomImage>
                        </div>
                        <p>USDT</p>
                      </button>
                    </li>
                    <li className="w-full h-[50px] items-center flex  font-nekstmedium hover:bg-[#3c3c3c] rounded-[10px]">
                      <button className="w-full h-full flex items-center px-[15px]">
                        <div className="flex items-center">
                          <CustomImage
                            width={20}
                            height={20}
                            alt="USDC"
                            src="/pages/p2p-trade/usdc.png"
                            className="mr-[3px]"
                          ></CustomImage>
                        </div>
                        <p>USDC</p>
                      </button>
                    </li>
                    <li className="w-full h-[50px] items-center flex  font-nekstmedium hover:bg-[#3c3c3c] rounded-[10px]">
                      <button className="w-full h-full flex items-center px-[15px]">
                        <div className="flex items-center">
                          <CustomImage
                            width={20}
                            height={20}
                            alt="BTC"
                            src="/pages/p2p-trade/btc.png"
                            className="mr-[3px]"
                          ></CustomImage>
                        </div>
                        <p>BTC</p>
                      </button>
                    </li>
                  </ul>
                </div>
                <div className="w-[200px] h-[35px] ">
                  <div className="w-[50%]">
                    <input type="number" className="h-[35px]" />
                  </div>
                  <div className="w-[50%]"></div>
                </div>
                <div></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
